          //var rutadev='http://localhost:8929/';  
          var rutaprod='https://mz-services.miraflores.gob.pe:8090/api/tramites/';
          axios.post(rutaprod+'tramite-web/',dataPost)
          //axios.post(rutadev,dataPost)
                    .then(response=>{
                      alert('Retorno');
                      console.log(response);
                      //console.log(this.listaTipoTramite[0].nombre);
                  })
                  .catch(e=>console.log(e))
      },
      validaNoDefinido(){

      },
      ObtenerUnidadesOrganicas(){
      //alert('Ingreso');
      //var rutadev='http://localhost:8929/';  
      var rutaprod='https://mz-services.miraflores.gob.pe:8090/api/tramites/';
      axios.get(rutaprod+'unidadorganica/'+this.reemplazaVacio(''))
                    .then(response=>{
                      this.listaUnidadesOrganicas=response.data.data;
                      console.log(response.data.data);
                      console.log(this.listaUnidadesOrganicas);
                      console.log(this.listaUnidadesOrganicas[0].uniorgnom);
                      var i=0;
                      while(i<this.listaUnidadesOrganicas.length){
                          var unidad={};
                          unidad.value=this.listaUnidadesOrganicas[i].uniorgcod;
                          unidad.text=this.listaUnidadesOrganicas[i].uniorgnom;
                          this.mapListUnidadesOrganicas.push(unidad);
                          i++;
                      }
                      console.log(this.mapListUnidadesOrganicas);
                  })
                  .catch(e=>console.log(e))
    }
  },computed:{
    selectOptions(){
      return this.listaUnidadesOrganicas.map(g => ({label: g.uniorgnom, value: g.uniorgnom}))
    },
    validaNoDefinidoCheck(){
        return this.marcado;
    }
  }
}
</script>


